.page {
  padding-bottom: var(--space-6);
}

.hero {
  padding: var(--space-6) 0 var(--space-4);
  border-bottom: 1px solid var(--accent);
  background: linear-gradient(160deg, rgba(255, 255, 255, 0.7), rgba(208, 199, 189, 0.35));
}

.hero h1 {
  font-size: clamp(2.2rem, 5vw, 3.4rem);
  margin-bottom: var(--space-2);
}

.subhead {
  color: var(--muted);
  max-width: 56ch;
}

.section {
  padding: var(--space-5) 0;
}

.sectionHeader {
  margin-bottom: var(--space-3);
}

.sectionHeader h2 {
  font-size: clamp(1.6rem, 3vw, 2.4rem);
}

.grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: var(--space-3);
}

.card {
  background: var(--card);
  border: 1px solid var(--accent);
  border-radius: var(--radius-md);
  padding: var(--space-3);
  box-shadow: 0 8px 18px rgba(17, 17, 17, 0.04);
  transition: transform var(--motion-medium) var(--ease),
    box-shadow var(--motion-medium) var(--ease);
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.card:hover {
  transform: translateY(-4px);
  box-shadow: 0 18px 32px rgba(17, 17, 17, 0.12);
}

.card h3 {
    margin: var(--space-2) 0 var(--space-1);
    font-size: 1.1rem;
}

.viewLink {
    display: inline-flex;
    align-items: center;
    gap: 0.4rem;
    color: #ffffff;
    font-weight: 600;
    font-size: 0.95rem;
    padding: 0.4rem 0.9rem;
    border-radius: 999px;
    border: 1px solid #111111;
    background: #111111;
    margin-top: auto;
}

.viewLink::after {
    content: \"â†’\";
    font-size: 0.95rem;
}

.cardMedia {
  position: relative;
  border-radius: var(--radius-sm);
  overflow: hidden;
  height: 100px;
  width: 100px;
  border: 1px solid rgba(17, 17, 17, 0.1);
  background: var(--media-primary);
}

.mediaPrimary,
.mediaHover {
  position: absolute;
  inset: 0;
  transition: opacity var(--motion-medium) var(--ease);
}

.mediaPrimary {
  background: linear-gradient(135deg, var(--media-primary), #ffffff);
  opacity: 1;
}

.mediaHover {
  background: linear-gradient(135deg, var(--media-hover), #ffffff);
  opacity: 0;
}

.card:hover .mediaHover {
  opacity: 1;
}

.card:hover .mediaPrimary {
  opacity: 0;
}

@media (prefers-reduced-motion: reduce) {
  .card {
    transition: none;
  }

  .mediaPrimary,
  .mediaHover {
    transition: none;
  }
}
